import * as Path from './interface.js'


let treeApi = {
	currencyStandardSelectionBoxTree(_this, params){//APP
		return _this.$http.post(Path.currencyStandardSelectionBoxTree, params)
		.then(res => {
			const result = res.data
			if(result.success){
				return result.data
			}else{
				throw result
				return []
			}
		})
		.catch(err => {
			_this.Static_GlobalFucs.showModal(_this,{
				title:_this.$i18nMsg("lang-form-modalTipsError"),
				content:err || "",
				showCancel:false
			})
			console.error(err)
		})
	},
	currencyStandardSelectionBoxSingleTree(_this, params, type){//APP
		//typeG-groupD-detail
		return _this.$http.post(Path.currencyStandardSelectionBoxSingleTree, params)
		.then(res => {
			const result = res.data
			if(result.success){
				let groupList = (result.data && result.data.groupList) || [],
					childList = (result.data && result.data.childList) || []
				return groupList.concat(childList)
			}else{
				throw result
				return []
			}
		})
		.catch(err => {
			_this.Static_GlobalFucs.showModal(_this,{
				title:_this.$i18nMsg("lang-form-modalTipsError"),
				content:err || "",
				showCancel:false
			})
			console.error(err)
		})
	},
	currencyGetRootNodes(_this,params){//
		return _this.$http.post(Path.currencyGetRootNodes, params)
		.then(res => { return res.data })
		.catch(err => {
			_this.Static_GlobalFucs.showModal(_this,{
				title:_this.$i18nMsg("lang-form-modalTipsError"),
				content:err || "",
				showCancel:false
			})
			console.log(err)
		})
	},
}

export default treeApi