import vue from 'vue'

const mutations = {
	STATE_formElementFocus(state,uid){
		state.formElementFocus = uid
	},
	STATE_INIT(state){
		state.mpvuePickerShow = false
		state.formElementFocus = false
		state.isOriginHei = true
	},
	SHOW_UPLOAD(state, show){//
		state.showUpload = show
	},
	SET_FRAMETYPE(state, frameType){//
		state.frameType = frameType
	},
	SET_VERSIONINFO(state, verisioninfo){//
		state.appVersionInfo = verisioninfo
	},
	SET_SCREEN_HEIGHT(state, screenHeight) {//screenHeight
		state.screenHeight = screenHeight
	},
	SET_SYSPARAMER(state, param){//
		state[param.key] = param.value 
	},
	SET_WATERMARK(state, param){//
		state.waterMarkConfig = param
	},
	SET_APPVERSION(state, version){
		state.currentVersion = version || ""
	},
	SET_LOGIN_STATE(state, params) {//
		state.userInfo = params
		state.userDesigner = params.userDesignerId || "" //
		state.userId = params.id || "" //id
		state.userKey = params.keycode || "" //
		state.userName = params.account || "" //
		state.account = params.account || "" //
		state.username = params.username || "" //
		state.usertelephone = params.telephone || "" //
		state.token = params.token || ""
		state.appVersion = params.appVersion || ""
		state.email = params.email || ""
		state.telephone = params.telephone || ""
		state.personalSignal = params.personalSignal || ""
		state.userImgPath = ''
		state.upgradeURL = params.upgradeURL || "",
		
		//
		state.formElementFocus = ''
		
		//
		state.upgradeMode = params.upgradeMode || 1,//
		state.appUploadUrl = {//app
			installAtionPackAgeURL: params.installAtionPackAgeURL || "",//
			androidDownloadUrl: params.androidDownloadUrl || "",//
			iosDownloadUrl: params.iosDownloadUrl || "",//ios
			upgradeURL: params.upgradeURL || ""//
		}
	},
	SET_USER_IMAGE(state, userImage){//
		state.userImage = userImage
	},
	SET_FORM_PickerShow(state,flag){//
		state.mpvuePickerShow = flag
	},
	SET_ORIGIN(state, params) {
		if(state.mpvuePickerShow) params = false //
		state.isOriginHei = params
	},
	SET_bundle(state, bundle) {//bundle
		state.bundleId = bundle.bundleId
		state.bundleName = bundle.bundleName || ''
	},
	SET_BusinessID(state, BusinessID) {//Id
		state.BusinessID = BusinessID
	},
	SET_GlobalIds(state, GlobalIds) {//
		state.GlobalIds = GlobalIds
	},
	UPDATE_USER_STATE(state, params) {//
		state.email = params.email
		state.telephone = params.telephone
		state.personalSignal = params.personalSignal
	},
	UPDATE_USERIMAGE_STATE(state, userImage) {//
		state.userImage = userImage
	},
	UPDATE_SERVERURL_STATE(state, params) {//url
		state.baseUrl = params
	},
	UPDATE_THEME_COLOR(state, params) {//url
		state.themeColor = params.color
		state.themeColorCode = params.colorCode
	}
}

export default mutations