import vue from 'vue'

const state = {
	ancestors:[],//
}

const mutations = {
	SETA_TREE_ANCESTORS(state,ancestors){//
		state.ancestors = ancestors
	}
}

export default {
	namespaced:true,
	state,
	mutations
}

