import store from '@/store/index'
import Request from './request'

const http = new Request();
const statusCodes = {
	400: '(400)',
	401: '(401)',
	403: '(403)',
	404: '(404)',
	406: '(406)',
	408: '(408)',
	410: '(410)',
	422: '(422)',
	500: '(500)',
	501: '(501)',
	502: '(502)',
	503: '(503)',
	504: '(504)',
	"request:fail timeout": ''
} 
let baseUrl = uni.getStorageSync('baseUrl') || store.state.defaultUrl
// 
http.setConfig((config) => { 
	config.baseUrl = baseUrl;
	config.header = {
		'content-type': 'application/json;charset=UTF-8',
		'Access-Control-Allow-Origin':'*',
	}
	return config
})
// 
http.interceptor.request((config, cancel) => { 
	let token = store.state.token
	if (!config.noToken) { 
		config.header.token = token
	}
	if(!config.noLoading){
		let url = config.url.split("?") || []
		store.commit("Toast/TOAST_REQUESTURLS",{
			type:"push",
			url:url[0]
		})
	}
	return config;
})
//
http.interceptor.response((response) => { 
	const config = response.config || {}
	let statusCode = response.statusCode || '',
		errMsg = response.errMsg || ''
	if(statusCode !== 200){
		response = statusCodes[statusCode] || statusCodes[errMsg]
		if(statusCode === 401 && !config.errorNoRefresh){//errorNoRefresh true401
			uni.reLaunch({
				url: '/pages/index/index'
			});
		}
	}
	if(!config.noLoading){
		let url = config.url.split("?") || []
		store.commit("Toast/TOAST_REQUESTURLS",{
			type:"out",
			url:url[0]
		})
	}
	
	return response;
})

export default http;


