import vue from 'vue'

const state = {
	ScanFramework:{
		static_scanResult:{}//
	},
	Inspections:{
		InspectionOrganization:{},//
		InspectionPosition:{},//
		InspectionDevice:{}, //
		InspectionId:{},//id
		InspectionPointId:{},//id
	}
}

const mutations = {
	Set_ScanFramework(state,param){//
		state.ScanFramework[param.key] = {...param.value}
	},
	Set_Inspections(state,param){//
		state.Inspections[param.key] = {...param.value}
	}
}

export default {
	namespaced:true,
	state,
	mutations
}