/**
 * 
 */
import {commonApi} from '@/common/request/api.js'
import { CHECKED_TYPE } from '@/common/constant/checkrules.js';
import { static_btnElementsInfo, static_basicDatas } from '@/common/constant/constant.js'
import { ELEMENT_BOTTOM_BTNS, ELEMENT_TOP_BTNS, ELEMENT_COMBOX, ELEMENT_SEARCHDATA } from '@/common/constant/element.js'
import graceChecker from '@/common/graceChecker/graceChecker.js' //
import Static_ParamFormat from '@/static/js/paramFormat.js'

import { COMMON_DATA_FORMAT, TRIGGRT_BaseValue, TRIGGRT_GetRule, TRIGGRT_Events } from '@/common/functions/trigger.js';
/**
 * @param {Object} element
 * @param {Object} param
 * 	{String mobileTabId}id
 */
function static_setRules(element,param){
	let checkrule = (element.requisite && element.visiabled) ? "notnull" :"",
		dataRule = CHECKED_TYPE[element.dataFormat] || "",
		ruleArray = []
	if(element.requisite && element.visiabled){
		let checkrule = "notnull"
		const elementTypes = [2,3,12,25,20,21,22]
		if(elementTypes.indexOf(element.baseType) !== -1 ) checkrule = "notnullSelect" //
		else if(element.baseType === 8) checkrule = "imgnotnull"//
		ruleArray.push(checkrule)
	} 
	if(dataRule) ruleArray.push(dataRule)
	let rule = {
		name:element.elementId,
		checkType : ruleArray.join(","), 
		checkRule:"",  
		label:element.label,
		errorMsg: element.label + "",
	}
	
	if(!param.mobileTabId) return ""
	else return rule
}
class Static_GlobalFucs {
	constructor(arg) {}
	//
	Permissiones(bundleId, basetype, formType) {
		const bundlePers = uni.getStorageSync("bundlePermissiones") || {},
			  bundlePer = bundlePers[bundleId]
		const element = static_btnElementsInfo.find( item => item.basetype === basetype) || {},
			  elementPer = element.permissiones || {},
			  elementNoPer = element.noPermissiones || []
		if(formType && elementNoPer.indexOf(formType) !== -1) return false
		
		let flag = false
		for (let key in elementPer) {
			if(elementPer[key] === bundlePer[key]) {
				flag = true
				break
			}
		}
		
		return flag
	}
	//sql
	getSQLParams(sql) {
		let sqlArry = sql.match(/(?=\{).*?(?=\})/g) || [],arry = []
		for(let i = 0;i<sqlArry.length;i++){
			arry.push(sqlArry[i].replace("{",""))
		}
		return  arry
	}
	//
	Global_dataFormat(value = '',_this){
		return COMMON_DATA_FORMAT(value)
	}
	/**
	 * 
	 */
	getGlobalParam(_this){//
		//
		const frameType = _this.$store.state.frameType//
		const FrameVariable = _this.$store.state.FrameVariable[frameType]
		return {
			userInfo:{..._this.$store.state.userInfo},
			CurrentUserID:{
				id:_this.$store.state.userId,
				name:_this.$store.state.username,
				relationColumnType:2
			},
			BusinessID:{
				id:_this.$store.state.BusinessID,
				name:_this.$store.state.BusinessID,
				relationColumnType:2
			},
			ParentID:{
				id:_this.$store.state.ParentID,
				name:_this.$store.state.ParentID,
				relationColumnType:2
			},
			GlobalIds:{
				id:_this.$store.state.GlobalIds.join(","),
				name:_this.$store.state.GlobalIds.join(","),
				relationColumnType:2
			},
			frameType,//
			FrameVariable//
		}
	}
	Global_ParamFormat(param = {},_this){
		param.storeValues = this.getGlobalParam(_this)
		return Static_ParamFormat(param)
	}
	/**
	 * @param {Object} param
	 * 	{String eventType}  4-23-1-
	 *  {Object triggerList} 
	 * 	{Array triggerElements } 
	 *  {Array oldElements } 
	 *  {Array currentElements } 
	 *  {Array currentBottomBtns } 
	 *  {Array currentTopRightBtns } 
	 *  {Array currentComboBoxs } 
	 *  {Objiect formData} 
	 */
	async formElementsTrigger(param,_this){
		let elements = param.triggerElements, //
			currentData = param.formData || {}//
			
		const storeValues = this.getGlobalParam(_this)//
		const btnsArray = [...ELEMENT_BOTTOM_BTNS,...ELEMENT_TOP_BTNS,...ELEMENT_COMBOX]
		const arrayGetValue = [...ELEMENT_SEARCHDATA,...[17]]
		
		const paramFormatConfig = {
			storeValues:storeValues,
			tabsElements:_this.tabsElements,
			tabsFormDatas:_this.tabsFormDatas
		}
		//isRecursion
		if(param.eventType === 4 && !param.isRecursion){ //
			for(let i = 0,len=elements.length;i<len;i++){
				const element = elements[i],
							{baseType, elementFormat, elementId, defaultValue, mainTableName, mainColumnName,
							 valueFieldType, isMultiple} = element
				if(baseType === 42) continue //
				if(baseType === 8 && elementFormat !== 2) continue
				//
				if(btnsArray.indexOf(baseType) !== -1 || baseType === 11) continue
				
				//
				let elementValue = currentData[elementId] || ''
				elementValue = COMMON_DATA_FORMAT(elementValue)//
				elementValue = {...elementValue, ...TRIGGRT_BaseValue(baseType,1,elementValue.id)}
				
				if(!elementValue.id && defaultValue) {//
					elementValue = Static_ParamFormat({...{
						key:defaultValue,
						otherTabId:param.relationMainTab,
						mobileTabId:param.mobileTabId,
						bundleParams:param.bundleParams
					},...paramFormatConfig})
					if(arrayGetValue.indexOf(baseType) !==-1 && mainTableName 
					   && mainColumnName && valueFieldType !== 2 && elementValue.id){
						//
						const key = `${mainTableName}_${elementValue.id}`
						const selectData = param.searchSelectInfo[key] || ''
						let selectvalue = {}
						if(!selectData){
							await this.getSearchSelectById(_this, elementValue.id,mainTableName,isMultiple).then(value => {
								param.searchSelectInfo[key] = value
								let result = {}
								//
								if(!isMultiple) result = {id:value.id,name:value[mainColumnName] || ""}
								else {
									let resultLabel = [],resultValue = []
									value.map(item => {
										resultValue.push(item.id)
										resultLabel.push(item[mainColumnName] || "")
									})
									result = {id:resultValue.join(","),name:resultLabel.join(",")}
								}
								selectvalue = result
							})
						}else{
							selectvalue = {
								id:selectData.id,
								name:selectData[mainColumnName] || ""
							}
						}
						elementValue = {...elementValue,...selectvalue}
					}
				}
				//
				elementValue = {...elementValue, ...TRIGGRT_BaseValue(element.baseType,1,elementValue.id)}
				elementValue.relationColumnType = element.relationColumnType
				elementValue.parameter = element.parameter //
				param.formData[element.elementId] = elementValue
			}
		}
		_this.SET_TABS_FORMDATAS({//
			tabId:param.mobileTabId,
			formData:param.formData
		})
		let isSascade = [],updateElement=[]
		for(let elementIndex = 0, len = elements.length; elementIndex < len; elementIndex ++){
			const element = elements[elementIndex],
						{baseType, elementFormat, elementId, defaultValue, mainTableName, mainColumnName,
						 valueFieldType, isMultiple,triggerSign} = element
			if(baseType === 11 || baseType === 42) continue //
			
			let elementValue = currentData[elementId] || {}
			//,
			const triggers = param.triggerList[elementId] || []
			//
			let triggerCur = triggers.filter( item => item.basetype === param.eventType && item.triggerType===1) || []
			if(triggerCur.length > 0){//
				let triggerEvents = TRIGGRT_Events({
					otherTabId:param.relationMainTab,
					mobileTabId:param.mobileTabId,
					element:element,
					triggerCur:triggerCur,
					value:elementValue && elementValue.id,
					type:triggerSign,// 1-
					paramFormatConfig:paramFormatConfig
				})
				//
				for(let triggerEventIndex = 0, len = triggerEvents.length; triggerEventIndex < len; triggerEventIndex ++){
					const triggerEvent = triggerEvents[triggerEventIndex],
						  compareType = triggerEvent.compareType
					let trigger = triggerEvent.triggers || []
					for(let triggerIndex = 0, len = trigger.length; triggerIndex < len; triggerIndex ++){
						console.log("getSearchSelectById"+triggerIndex)
						const triggerItem = trigger[triggerIndex]
						const relationElementId = triggerItem.relationElementId,
							  relationElementOldValue = param.oldFormData[relationElementId] || {},
							  relationOldElementIndex = param.oldElements.findIndex(item => item.elementId === relationElementId),
							  relationOldElement = relationOldElementIndex === -1 ? '': param.oldElements[relationOldElementIndex]
						
						let relationElement = {...relationOldElement}
						if(!relationElement) continue
						
						let triggerParam = {}
						if(compareType) {//,
							triggerParam = {
								requisite: triggerItem.relationElementRequisite,
								readonly: triggerItem.relationElementReadonly,
								visiabled: triggerItem.relationElementVisible
							}
						}
						
						//
						if(relationElement.baseType === 42){//
							if(triggerItem.canModifyValue) updateElement.push(relationElementId)
							const index = param.currentTabsChilds.findIndex( item => item.elementId === relationElementId)
							param.currentTabsChilds[index] =  {...relationElement, ...triggerParam}
							//
							if(param.eventType === 4) param.oldElements[relationOldElementIndex] = {...relationElement, ...triggerParam}
						}else if(ELEMENT_BOTTOM_BTNS.indexOf(relationElement.baseType) !== -1){//
							const index = param.currentBottomBtns.findIndex( item => item.elementId === relationElementId)
							param.currentBottomBtns[index] =  {...relationElement, ...triggerParam}
							//
							if(param.eventType === 4) param.oldElements[relationOldElementIndex] = {...relationElement, ...triggerParam}
						}else if(ELEMENT_TOP_BTNS.indexOf(relationElement.baseType) !== -1){//
							const index = param.currentTopRightBtns.findIndex( item => item.elementId === relationElementId)
							param.currentTopRightBtns[index] =  {...relationElement, ...triggerParam}
							//
							if(param.eventType === 4) param.oldElements[relationOldElementIndex] = {...relationElement, ...triggerParam}
						}else if(ELEMENT_COMBOX.indexOf(relationElement.baseType) !== -1){//
							const index = param.currentComboBoxs.findIndex( item => item.elementId === relationElementId)
							param.currentComboBoxs[index] = {...relationElement, ...triggerParam}
							//
							if(param.eventType === 4) param.oldElements[relationOldElementIndex] = {...relationElement, ...triggerParam}
						}else{//
							const index = param.currentElements.findIndex( item => item.elementId === relationElementId)
							const currentE = {...relationElement, ...triggerParam}
							param.currentElements[index] = currentE
							//
							if(param.eventType === 4) param.oldElements[relationOldElementIndex] = {...relationElement, ...triggerParam}
							
							//
							if(compareType){
								if(!triggerItem.relationElementValue) continue//
								
								let selectDatas =  param.searchSelectInfo || {}
								let relationElementValue = ""
								if(triggerItem.relationElementValue === '@@InitValue'){//
									relationElementValue = {...relationElementOldValue}
								}else{
									const key = `${mainTableName}_${elementValue.id}`
									let selectData = selectDatas[key]
									
									//selectData
									if(arrayGetValue.indexOf(baseType) !==-1 && mainTableName 
									  && mainColumnName && valueFieldType !== 2 && elementValue.id
										&& !selectData){
										await this.getSearchSelectById(_this,elementValue.id,mainTableName,isMultiple).then(value => {
											selectData = value
											param.searchSelectInfo[key] = value
										})
									}
									relationElementValue = Static_ParamFormat({
										key:triggerItem.relationElementValue,
										otherTabId:param.relationMainTab,
										mobileTabId:param.mobileTabId,
										storeValues:storeValues,
										tabsElements:_this.tabsElements,
										tabsFormDatas:_this.tabsFormDatas,
										data:selectData || {}//
									})
								}
								
								relationElementValue = COMMON_DATA_FORMAT(relationElementValue) //
								relationElementValue.relationColumnType = relationElement.relationColumnType
								//lable(lable)getSearchSelectById
								if(arrayGetValue.indexOf(relationElement.baseType) !==-1 && relationElement.mainTableName && relationElement.mainColumnName && relationElement.valueFieldType !== 2 && relationElementValue.id){
									let selectvalue = {},
										isGetById = false
									//
									let {mainTableName:rmainTableName,mainColumnName:rmainColumnName,
									     isMultiple:risMultiple
											} = relationElement
									const key = `${rmainTableName}_${relationElementValue.id}`
									const selectData = param.searchSelectInfo[key] || ''
									if(!selectData){
										await this.getSearchSelectById(_this, relationElementValue.id,rmainTableName,risMultiple).then(value => {
											param.searchSelectInfo[key] = value
											let result = {}
											//
											if(!risMultiple) result = {id:value.id,name:value[rmainColumnName] || ""}
											else {
												let resultLabel = [],resultValue = []
												value.map(item => {
													resultValue.push(item.id)
													resultLabel.push(item[rmainColumnName] || "")
												})
												result = {id:resultValue.join(","),name:resultLabel.join(",")}
											}
											selectvalue = result
										})
									}else{
										let result = {}
										//
										if(!risMultiple) result = {id:selectData.id,name:selectData[rmainColumnName] || ""}
										else {
											let resultLabel = [],resultValue = []
											selectData.map(item => {
												resultValue.push(item.id)
												resultLabel.push(item[rmainColumnName] || "")
											})
											result = {id:resultValue.join(","),name:resultLabel.join(",")}
										}
										selectvalue = result
									}
									relationElementValue = {...relationElementValue,...selectvalue}
								}
								if(param.eventType === 4) param.oldFormData[relationElementId] = {...relationElementValue}
								param.formData[relationElementId] = {...relationElementValue}
							}else{//
								if(param.eventType === 4) param.oldFormData[relationElementId] = {...relationElementOldValue}
								param.formData[relationElementId] = {...relationElementOldValue}
							}
						}
						//
						const triggers = param.triggerList[relationElementId] || []
						let triggerCur = triggers.filter( item => item.basetype === param.eventType && item.triggerType===1) || []
						if(triggerCur.length>0 && compareType) isSascade.push(relationElement)
					}
				}
			}
		}
		console.log(updateElement)
		if(updateElement.length>0){//id
			const old = param.updateElement || []
			param.updateElement = [...old,...updateElement]
		}
		
		if(isSascade.length>0) {
			param.triggerElements = isSascade
			param.isRecursion = true
			return this.formElementsTrigger(param,_this)
		}else{
			return param
		}
	}
	
	//
	getSearchSelectById(_this, value, mainTableName,isMultiple){
		return commonApi.getSearchSelectById(_this,{
			id:value,
			tabSource:mainTableName
		},isMultiple)
	}
	//
	/**
	 * @param {Object} value
	 */
	dataFormat(value=""){
		try{//json
			value = JSON.parse(value)
		}catch(e){}
		
		if(value && typeof value !== "object"){
			value = {
				id:value,
				name:value
			}
		}
		
		return value
	}
	//
	showToast(params){
		let baseConfig = {
			title:"",
			icon:"none"
		}
		params = {...baseConfig,...params}
		uni.showToast(params);
	}
	showModal(_this,params,type){
		let baseConfig = {
			title:_this.$i18nMsg("lang-form-modalTips"),
			content:"",
			cancelText:_this.$i18nMsg("lang-form-modalConfirm"),
			confirmText:_this.$i18nMsg("lang-form-modalConfirm")
		}
		if(type==="error") baseConfig.title = _this.$i18nMsg("lang-form-modalTipsError")
		
		params = {baseConfig,...params}
		uni.showModal(params)
	}
	//form
	checkForm(params){
		const rule = params.rule || [],
			  formData = params.data || []
		
		return {
			isCheck:graceChecker.check(formData, rule),
			errorMsg:graceChecker.error
		}
	}
	//id
	setBusinessID(_this, businessID){
		let data = this.dataFormat(businessID)
		_this.SET_BusinessID(data.id)
		return data
	}
	/** 
	 * @param {Object} 
	 * @param {String} 
	 */
	gotoOtherRoute(params,static_mobileTabShowType){
		let url = `/pages/${static_mobileTabShowType}?relationTab=${params.tabId}`
		uni.navigateTo({
			url:url
		})
	}
}

export default Static_GlobalFucs