import vue from 'vue'
const homeTemplate = uni.getStorageSync("homeTemplate") || {}//
const state = {
	calendarBundle:{},//bundle
	newBundle:{},//bundle
	figureList: [],//
	leftList:[{
		"iconName":"iconsaoma"
	}],//
	rightList: [{
		"iconName":"iconxiaoxi"
	}],//
	topCoreList: homeTemplate.topCoreList || [],//
	workSpaceList: homeTemplate.workSpaceList || [],//
	bottomList: homeTemplate.bottomList || [],
	myWorkSpace: homeTemplate.bottomList || [],//
	countsql: {},
	enabledData: {}
}
const mutations = {
	CLEAR_STATE(state){//
		state.calendarBundle={}//bundle
		state.newBundle={}//bundle
	},
	SET_HOME_TEMPLATE(state, params){//
		const {topCoreList, workSpace, figureList, bottomList, myWorkSpace} = params
		state.topCoreList = topCoreList
		state.workSpaceList = workSpace
		state.figureList =  figureList
		state.myWorkSpace = myWorkSpace
		state.bottomList = bottomList.map(item => {item.barKey = item.id; return item})
	},
	SET_NEW_BUNDLE(state, params){//bundle
		vue.set(state.newBundle, params.bundle,params.isNew)
	},
	SET_CALENDAR_BUNDLE(state, params) {//bundle
		if(state.calendarBundle[params.bundle]) state.calendarBundle[params.bundle] = {...params.calendarBundle}
		else vue.set(state.calendarBundle, params.bundle, params.calendarBundle)
	},
	SET_SELECT_INFO(state, params){
		state.enabledData=params//
	},
	SET_COUNT_SQL(state, params){
		state.countsql=params//
	},
}

export default {
	namespaced:true,
	state,
	mutations
}

