const interFaceInParamByTab = 'serviceParam/selectByTabId' //id
/**
 * @param {Number} commonApiHeaderId id
 * @param {Number} mobileTabId id
 */
export const sourceInterface_getInParamByTab = (param , _this) => {
	return _this.$http.post(interFaceInParamByTab, param)
	.then(res => {
		const result = res.data || {}
		return result && result.data
	})
	.catch(err => {
		uni.showModal({
			title:_this.$i18nMsg("lang-form-modalTipsError"),
			content:result.msg,
			showCancel:false,
			confirmText:_this.$i18nMsg("lang-form-modalConfirm")
		})
		return []
	})
}